import s from './App.module.css';
import Header from './components/Header';
import Stats from './components/Stats';
import AdminWallet from './components/AdminWallet';
import Btn from './components/AdminBtn';
import Wallet from './components/Wallet';
import Card from './components/Card';
import React, { useState } from 'react';

function App({ client }) {
  const [balance1, setBalance1] = useState({ uax: "-", gas: "-" });
  const [balance2, setBalance2] = useState({ uax: "-", gas: "-" });
  // eslint-disable-next-line
  const [account1, _] = useState({ address: "0:911d8d474b584cb4a3eb21a02c70cd2172054e2455d2472a7151b7986ffbe0d6" })
  // eslint-disable-next-line
  const [account2, __] = useState({ address: "0:c892a0387f157153cea7d7b244c6c54ded126ade017bd2c4feb0d3044a643b52" })

  const updateBalances = async (newb, newb2) => {
    setBalance1();
    setBalance2();
  }

  return (
    <main className={s.main}>
      <div className={s.desk}>
        <Header title="Dashboard" />
        <div className={s.flex}>
          <aside className={s.aside}>
            <Stats
              UAXTotal="8,000,000"
              UserTotal="537"
              BaseFee="12"
              FeeTotal="23,889"
              GiverTotal="2,000,000"
              Transactions="4,221"
            />
            <AdminWallet>
              <Btn title="Update wallet 1" client={client} account={account1} setBalance={setBalance1} />
              <Btn title="Update wallet 2" client={client} account={account2} setBalance={setBalance2} />
              {/* <Btn title="Speed up" />
              <Btn title="Speed down" />
              <Btn title="Clear desk" /> */}
            </AdminWallet>
          </aside>
          <div className={s.page}>
            <div className={s.wallet}>
              <Wallet uax={balance1.uax} gas={balance1.gas} account={account1} />
              <Card title="Transfer" label="Value (uax)" buttonText="Send" client={client} from={account1} to={account2} then={updateBalances} />
            </div>
            <div className={s.wallet}>
              <Wallet uax={balance2.uax} gas={balance2.gas} account={account2} />
              <Card title="Transfer" label="Value (uax)" buttonText="Send" client={client} from={account2} to={account1} then={updateBalances} />
            </div>
          </div>
        </div>
      </div>
    </main>
  );
}

export default App;
