import s from './AdminBtn.module.css';
import TokenWalletABI from '../../abi/TokenWallet.abi.json';
import { Account } from '@tonclient/core';

const TokenWallet = {
  abi: TokenWalletABI
}

function Btn({ title, client, account, setBalance }) {

  async function updateWallet() {
    const wallet = new Account(TokenWallet, { address: account.address, client });

    const balanceResponse = await wallet.runLocal('_balance');
    const balanceUAX = balanceResponse.decoded.output._balance;

    const balanceCrystalHex = (await client.net.query_collection({
      collection: 'accounts',
      filter: {
        id: { eq: account.address },
      },
      result: 'balance',
    })).result[0].balance;
    const balanceCrystal = parseInt(balanceCrystalHex, 16) / 10 ** 9;

    console.log(balanceUAX);
    console.log(balanceCrystal);

    setBalance({ uax: balanceUAX, gas: balanceCrystal.toString() })
  }

  return (
    <button className={s.button} type="button" onClick={updateWallet}>
      {title}
    </button>
  );
}

export default Btn;
