import s from './Card.module.css';
import { Account } from '@tonclient/core'
import TokenWalletABI from '../../abi/TokenWallet.abi.json';

const TokenWallet = {
  abi: TokenWalletABI
}

function Card({ title, label, buttonText, client, from, to, then, children }) {

  async function transfer() {
    const wallet = new Account(TokenWallet, { address: from.address, client });
    let res = await wallet.run("transferTokens", { to: to.address, val: 1 });
    console.log(res);
  }

  return (
    <section className={s.card}>
      <h2 className={s.title}>{title}</h2>
      <form className={s.block}>
        <label className={s.label}>Recipient (hash)</label>
        <input
          className={s.input}
          type="text"
          placeholder="Enter recipient address"
          defaultValue={to.address}
        />
        <label className={s.label}>{label}</label>
        <input className={s.input} type="text" placeholder="1,000" />
        {children}
        <button className={s.button} type="submit" onClick={transfer(from.address, to.address, 1).then(then)}>
          {buttonText}
        </button>
      </form>
    </section>
  );
}

export default Card;
